@import '../../Utils/variables.scss';
@import '../../Utils/mixins.scss';

.Overlay {
  width: 100%;
  height: 100%;
  font-size: 12pt;
  position: fixed;
  overflow: hidden;
  z-index: 99;
  
  &__backgroundCover {
    width: 100%;
    height: 100vh;
    background-color: $background-color;
    position: fixed;
    top: 0;
    z-index: 98;
  }

  &__art {
    height: 100vh;
    position: relative;
    overflow: hidden;
    z-index: 1;
  }

  &__abstract {
    width: 130%;
    // filter: saturate(100%);

    &--path {
      will-change: stroke-dashoffset;
      stroke-dasharray: 770 770;
      stroke-dashoffset: 770;
    }
  }

  &__glow {
    width: 150%;
    top: 47.5% !important;
    opacity: 0;
    // filter: saturate(150%);
  }

  &__iconWrapper {
    font-size: 12pt;
    height: 2em;
    width: 2em;
    position: fixed;
    top: 1em;
    right: 0.75em;
    z-index: 100;
  }

  &__icon {
    width: 2em;
    height: 0.35em;
    background-color: $primary-text-color;
    position: relative;

    &--top {
      right: 0.5em;
    }

    &--bottom {
      top: 0.5em;
    }
  }

  &__linkWrapper {
    z-index: 2;
    padding-right: 5%;
  }
  
  &__link {
    font-size: 3em;
    font-weight: bold;
    font-style: italic;
    text-transform: uppercase;
    text-align: center;
    text-shadow: 3px 3px 10px $shadow-color;
    color: $primary-text-color;
    position: relative;
    opacity: 0;

    &--home     { left: 0; }
    &--projects { left: 0.35em; }
    &--about    { left: -0.75em; }
    &--contact  { left: 0.55em; }
  }

  &__socialIconWrapper {
    width: 100%;
    position: absolute;
    bottom: 2%;
    display: flex;
    justify-content: center;
    z-index: 2;
  }

  &__socialIcon {
    height: 1.6em;
    margin: 0 0.7em;
    stroke-width: 2;

    &--GitHub {
      stroke-dasharray: 395 395;
    }

    &--Medium {
      stroke-dasharray: 485 485;
    }

    &--LinkedIn {
      stroke-dasharray: 265 265;
    }
  }
}

// modifer class
.center {
  @include center;
}

.preventClick {
  pointer-events: none;
}